<h2>Whole Grain Food </h2>
<p>
&emsp;Whole grain food are rich in vitamin B and magnesium that contribute to cognitive development responsible for your memory, problem-solving, and decision-making skills.
<br><br>
&emsp;A diet high in whole grain breads, cereals, brown rice, and yes, even popcorn, can also boost the blood flow to the brain.
