<h2>Nuts</h2>
<p>
&emsp;Walnuts, almonds, pecans, and peanuts can do wonders for your brain as they do almost everything—from clearing up that “brain fog” to making you think clearer and enhancing your mood.
<br><br>
&emsp;Walnuts contain Omega-3 fatty acids that help your cell membranes function properly. They also contain serotonin that controls mood and appetite. And if you are having difficulty sleeping, just add walnuts to your diet as they’ve been proven to relieve disorders like insomnia, depression, overeating, and other compulsive behavior.
<br><br>
&emsp;Almonds help generate natural mood-boosting neurotransmitters such as dopamine, adrenaline, and noradrenaline. They are also high in riboflavin which is known to boost the memory. Pecan and peanuts contain choline, which aids in memory and brain development.
</p>
<h2>Blueberries</h2>
<p>
&emsp;Suffering from memory loss? Research has shown that eating a cup of blueberries a day for two weeks will help improve the memory. Moreover, blueberries have a high level of antioxidants that neutralize the damaging effects of free radicals in the body. 
<br><br>
&emsp;Aside from reducing the risks of Alzheimer’s and dementia, a diet of blueberries also improves motor skills and reverses age-related declines in balance and coordination.
</p>
